.signup-form{
    border-radius: 10px;
    box-shadow: 0px 5px 20px rgb(71, 71, 71);
    margin: 20px;
    display: flex;
    flex-direction: column;
    justify-content: space-around;
    background: white;
    align-items: center;
    min-width: 40%;
    padding-top: 3%;
    padding-bottom: 3%;
    padding-left: 2%;
    padding-right: 2%;
}
input {
    border-left-color: #eb5362;
    border-right-color: #eb5362;
    border-top-color: #eb5362;
    border-bottom-color: #eb5362;
}

@media (max-width:512px) {
    .signup-form {
        display: grid
    }
    .signup-form input, button {
        grid-column: 1;
        grid-row: 1;
    }

    .signin-form{
        display: grid
    }

    .signup-form input, button {
        grid-column: 1;
        grid-row: 1;
    }
}