.bg-primary {
    background: #dc3545;
    background: var(--primary-color);
    color: #fff;
}

.navbar {
    display: flex;
    justify-content: space-between;
    align-items: center;
    width: 100%;
    /* opacity: 0.9; */
    margin-bottom: 1rem;
    position: fixed;
    top: 0;
    z-index: 9999;
}

.navbar ul {
    display: flex;
}

.navbar a {
    color: #fff;
    padding: 1.45rem;
    margin: 0 0.25rem;
}

.navbar a:hover {
    color: #f4f4f4;
    color: var(--light-color);
}

.navbar .welcome span {
    margin-right: 0.6rem;
}

.navbar {
