.recipe {
    border-radius: 10px;
    box-shadow: 0px 5px 20px rgb(71, 71, 71);
    margin: 20px;
    display: flex;
    flex-direction: column;
    justify-content: space-around;
    background: white;
    background-image: url('https://previews.123rf.com/images/happydancing/happydancing1209/happydancing120900034/15495787-old-book-with-blank-stained-pages-for-background.jpg');
    background-size: cover;
    align-items: center;
    min-width: 40%;
    max-width: 800px;
    padding-bottom: 3%;
    padding-left: 2%;
    padding-right: 2%;
    
}


.image {
    border-radius: 50%;
    width: 20vw;
    height: 20vw
}

@media (max-width:512px) {
    .navbar ul {
        display: block;
    }
